import React from 'react';
import { format, toDate } from 'date-fns';
// redux
import { useDispatch, useSelector } from 'react-redux';
import { addFamilyMember, updateFamilyMember } from '../../../redux/slices/familyMembersSlice';
// Services
import toastService from '../../../services/toast/toastService';
// MUI Components 
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
// Page Component
import Birthdate from './fields/Birthdate';
import Relations from './fields/Relations';
import YearOrMonth from './fields/YearOrMonth';
import ContactNumber from './fields/ContactNumber';
import YesOrNoSelect from './fields/YesOrNoSelect';  

export default function OnBoardingFamilyForm(props) {

    const dispatch = useDispatch();
    const familyMembersRedux = useSelector((state) => state.familyMembers.value);
    const selectedFamilyMember = familyMembersRedux.selectedFamilyMember;

    const bday = selectedFamilyMember?.birthdate !== undefined ? Date.parse(selectedFamilyMember?.birthdate) : null;
 
    const [name, setName] = React.useState(selectedFamilyMember?.name ?? "");
    const [birthdate, setBirthdate] = React.useState(bday);
    const [relations, setRelations] = React.useState(selectedFamilyMember?.relation ?? "");
    const [selectedRelations, setSelectedRelations] = React.useState(selectedFamilyMember?.selectedRelations ?? "");
    const [isBeneficiary, setIsBeneficiary] = React.useState(selectedFamilyMember?.isBeneficiary ?? "");
    const [spouseOccupation, setSpouseOccupation] = React.useState(selectedFamilyMember?.spouseOccupation ?? "");
    const [spouseContactNumber, setSpouseContactNumber] = React.useState(selectedFamilyMember?.spouseContactNumber ?? "");
    const [spouseLengthInService, setSpouseLengthInService] = React.useState(selectedFamilyMember?.spouseLengthInService ?? "");
    const [spouseYearOrMonth, setSpouseYearOrMonth] = React.useState(selectedFamilyMember?.spouseYearOrMonth ?? "");


    const handleSelectedRelations = (relation) => {
        setSelectedRelations(relation);
    }

    const handleStore = async () => {
        try {

            let bdate = format(toDate(birthdate), "Y-M-d");

            let data = {
                name,
                birthdate: bdate,
                relation: relations,
                selectedRelations,
                isBeneficiary,
            }

            if (relations.toLowerCase() === "spouse") {
                data = {
                    name,
                    birthdate: bdate,
                    relation: relations,
                    selectedRelations,
                    isBeneficiary,
                    spouseOccupation,
                    spouseContactNumber,
                    spouseLengthInService,
                    spouseYearOrMonth,
                }

            }

            dispatch(addFamilyMember(data));

            props.toggle();

        } catch (error) {
            toastService.error(error)
        }
    }

    const handleUpdate = async () => {
        try {

            let bdate = format(toDate(birthdate), "Y-M-d");

            let data = {
                name,
                birthdate: bdate,
                relation: relations,
                selectedRelations,
                isBeneficiary,
            }

            if (relations.toLowerCase() === "spouse") {
                data = {
                    name,
                    birthdate: bdate,
                    relation: relations,
                    selectedRelations,
                    isBeneficiary,
                    spouseOccupation,
                    spouseContactNumber,
                    spouseLengthInService,
                    spouseYearOrMonth,
                }
            }

            dispatch(updateFamilyMember(data));

            props.toggle();

        } catch (error) {
            toastService.error(error)
        }
    }

    const handleSubmit = () => {
        if (props.action.toLowerCase() === "edit") {
            handleUpdate();
        } else {
            handleStore();
        }
    }

    const handleChangeLengthInService = (value) => {

        setSpouseYearOrMonth("");

        let serviceLength = parseInt(value);
        let label = "Month";
        
        if (spouseYearOrMonth !== "") {
            if (spouseYearOrMonth === "Year" || spouseYearOrMonth === "Years") {
                label = "Year";
            } 
        }

        // Checking if plural or not
        if (serviceLength > 1) {
            label = `${label}s`;
        }

        setSpouseLengthInService(value);
        setSpouseYearOrMonth(label);

    }

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {

        }

        return () => mounted = true;
    }, [dispatch, selectedFamilyMember]);

    return (
        <React.Fragment>
            <Grid container spacing={2}>
                <Grid item xs={6} sx={{ mt: 2 }}>
                    <YesOrNoSelect yesOrNo={isBeneficiary} setYesOrNo={setIsBeneficiary} label="Is he/she your beneficiary?" />
                </Grid>
                <Grid item xs={6} sx={{ mt: 2 }}>
                    <Relations
                        setSelectedRelations={(relation) =>
                            handleSelectedRelations(relation)}
                        selectedRelations={selectedRelations}
                        relations={relations}
                        setRelations={(relation) => setRelations(relation)}
                    />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        onChange={(e) => setName(e.target.value)}
                        value={name}
                        variant="outlined"
                        size="small"
                        required
                        fullWidth
                        id="fullname"
                        label="Full Name"
                        name="fullname"
                    />
                </Grid>
                <Grid item xs={relations.toLowerCase() === "spouse" ? 6 : 12}>
                    <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} variant="outlined" />
                </Grid>
                {
                    (relations.toLowerCase() === "spouse") &&
                    <React.Fragment>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseContactNumber(e.target.value)}
                                value={spouseContactNumber}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="spouseContactNumber"
                                label="Spouse Mobile No."
                                name="spouseContactNumber"
                                InputProps={{
                                    inputComponent: ContactNumber
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseOccupation(e.target.value)}
                                value={spouseOccupation}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="spouseOccupation"
                                label="Spouse Occupation"
                                name="spouseOccupation"
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <YearOrMonth
                                fullWidth={true}
                                value={spouseLengthInService}
                                setServiceLength={(value) => handleChangeLengthInService(value)}
                                yearOrMonth={spouseYearOrMonth}
                                setYearOrMonth={setSpouseYearOrMonth}
                            />
                        </Grid>
                    </React.Fragment>
                }
            </Grid>
            <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ mt: 2 }}>
                <Button
                    sx={{ width: 150 }}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    onClick={() => handleSubmit()}
                >
                    {props.action.toLowerCase() === "edit" ? "Save" : "Add"}
                </Button>
                <Button
                    sx={{ width: 150, ml: 1 }}
                    type="button"
                    fullWidth
                    variant="contained"
                    color="secondary"
                    onClick={() => props.toggle()}
                >
                    Cancel
                </Button>
            </Box>
        </React.Fragment>
    )
}
