import * as React from 'react';
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';
import { format } from 'date-fns';
// redux
import { useSelector, useDispatch } from 'react-redux';
import { clearSelectedFamilyMember, removeFamilyMember, selectFamilyMembers, setIndex } from '../../../redux/slices/familyMembersSlice';
// MUI Components
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import NewFamilyMemberModal from '../modals/NewFamilyMemberModal';
// react-icons
import { FaEdit, FaTrash, FaPlusCircle } from 'react-icons/fa';



export default function NewFamilyMemberTable(props) {

    const dispatch = useDispatch();
    const familyMembersRedux = useSelector((state) => state.familyMembers.value);


    const [action, setAction] = React.useState("New");
    const [showAddFamilyMember, setShowAddFamilyMember] = React.useState(false);
    const toggleShowFamilyMember = () => setShowAddFamilyMember(!showAddFamilyMember);

    const handleAdd = () => {
        setAction("New");
        dispatch(clearSelectedFamilyMember());
        toggleShowFamilyMember();
    }

    const handleEdit = (rowData, index) => {
        setAction("Edit");
        dispatch(setIndex(index));
        dispatch(selectFamilyMembers(rowData));
        toggleShowFamilyMember();
    }

    const handleRemove = (index) => {
        let result = alertService.delete(1, "Remove this record?");

        result.then(opt => {
            if (opt.isConfirmed) {
                dispatch(setIndex(index));
                dispatch(removeFamilyMember());
            }
        })
    }

    return (
        <div style={{ width: '100%', marginBottom: 15 }}>
            {showAddFamilyMember && <NewFamilyMemberModal toggle={toggleShowFamilyMember} open={showAddFamilyMember} action={action} />}
            <Box display="flex" flexDirection="row" justifyContent="left" sx={{ mb: 2 }}>
                <Button variant="contained" sx={{ mt: 1 }} onClick={() => handleAdd()}>Add <FaPlusCircle style={{ marginLeft: 5 }} /></Button>
            </Box> 
            <TableContainer component={Paper} sx={{ maxHeight: 250 }}>
                <Table stickyHeader>
                    <TableHead>
                        <TableRow>
                            <TableCell sx={{ minWidth: 100 }}>Name</TableCell>
                            <TableCell sx={{ minWidth: 100 }}>Birthdate</TableCell>
                            <TableCell align='center' sx={{ minWidth: 100 }}>Relation</TableCell>
                            <TableCell align='center' sx={{ minWidth: 100 }}>Beneficiary</TableCell>
                            <TableCell align='center' sx={{ minWidth: 100 }}>Options</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody sx={{ overflowY: 'scroll', overflowX: 'hidden', height: 110 }}>
                        {familyMembersRedux.familyMembers.map((row, index) => {  
                            let birthdate = "N/A";
                            if (row.birthdate !== null) {
                                birthdate = format(Date.parse(row.birthdate), 'MMMM dd, yyyy');
                            }

                            return (
                                <TableRow
                                    key={`row-${index}`}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {row.name}
                                    </TableCell>
                                    <TableCell >{birthdate}</TableCell>
                                    <TableCell align="center">{utilityService.capitalizeFirstLetter(row.relation)}</TableCell>
                                    <TableCell align="center">{row.isBeneficiary ? "Yes" : "No"}</TableCell>
                                    <TableCell align="center">
                                        <Button color="primary" sx={{ minWidth: 30 }} onClick={() => handleEdit(row, index)}>
                                            <FaEdit />
                                        </Button>
                                        <Button color="secondary" sx={{ minWidth: 30 }} onClick={() => handleRemove(index)}>
                                            <FaTrash />
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            )
                        })}
                        {familyMembersRedux.familyMembers.length <= 0 &&
                            <TableRow >
                                <TableCell colSpan={5} component="th" scope="row" align='center'>
                                    No Records Added
                                </TableCell>
                            </TableRow>
                        }
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
}
