import React from 'react';
import OnboardingForm from '../forms/OnboardingForm';
import OnboardingDialog from '../shared/Dialog/OnboardingDialog';

export default function OnboardingModal(props) {
    return (
        <div>
            <OnboardingDialog open={props.open}>
                <OnboardingForm toggle={props.toggle} />
            </OnboardingDialog>
        </div>
    )
}
