import React from 'react';

// MUI Components
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
// react-icons
// react-icons
import { FaCheckCircle } from 'react-icons/fa';
import { BiCircle } from 'react-icons/bi';

const SourceOfFunds = (props) => {

    const [sourceOfFunds, setSourceOfFunds] = React.useState(props.sourceOfFunds ?? '');
    const [selected, setSelected] = React.useState(false);

    const sourceOfFundsOptions = [
        {
            value: "salary",
            label: "Salary"
        },
        {
            value: "business",
            label: "Business"
        },
    ];

    const handleChange = (value) => {

        if (sourceOfFunds !== value) {
            setSourceOfFunds(value);
            if (props.setSourceOfFunds) {
                props.setSourceOfFunds(value)
            }
            setSelected(true);
        } else {
            setSourceOfFunds("");
            if (props.setSourceOfFunds) {
                props.setSourceOfFunds("")
            }
            setSelected(false);
        }
    }

    React.useEffect(() => {
        let mounted = false;

        if(!mounted){
            if(props.sourceOfFunds !== ""){
                setSelected(true);
            }
        }

        return () => mounted = true;
        
    },[props.sourceOfFunds])

    return (
        <div>

            {
                sourceOfFundsOptions.map((option) => (
                    <FormControlLabel
                        key={`key-${option.label}`}
                        control={
                            <Checkbox
                                icon={<BiCircle size={20} />}
                                checkedIcon={<FaCheckCircle size={20} />}
                                required={!selected}
                                checked={sourceOfFunds.toLowerCase() === option.value}
                                onChange={() => handleChange(option.value)}
                            />
                        } label={option.label} />
                ))
            }

        </div>
    )
}

export default SourceOfFunds;