
import React, { useRef } from 'react';
import { format } from 'date-fns';
// Services
import storage from '../../../services/storage/storageService';
import toastService from '../../../services/toast/toastService';
import alertService from '../../../services/alert/alertService';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { put } from '../../../redux/async/membershipAsync';
import { setIsFirstLogin } from '../../../redux/slices/systemSlice';
import { resetForm } from '../../../redux/slices/membershipSlice';
import { clearFamilyMembers } from '../../../redux/slices/familyMembersSlice';
import { me as getMe } from '../../../redux/async/authAsync';
// Hooks
import useMember from '../../../hooks/useMember';
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import LoadingButton from '@mui/lab/LoadingButton';
// Form Components
import Gender from './fields/Gender';
import Nationality from './fields/Nationality';
import CivilStatus from './fields/CivilStatus';
import Birthdate from './fields/Birthdate';
import ContactNumber from './fields/ContactNumber';
import ProfilePhoto from '../shared/ProfilePhoto/ProfilePhoto';
import FileDropzone from '../shared/FileDropzone/FileDropzone';
import SourceOfFunds from './fields/SourceOfFunds';
import EmploymentTypes from './fields/EmploymentTypes';
import OrganizationTypes from './fields/OrganizationTypes';
import YearOrMonth from './fields/YearOrMonth';
import BusinessTypes from './fields/BusinessTypes';
import Country from './fields/Country';
import Hi from '../../../../assets/hi.jpg';
import NewFamilyMemberTable from '../tables/NewFamilyMemberTable';
import Zipcode from './fields/Zipcode';
// react-icons
import { FaArrowRight, FaArrowLeft, FaCheck, FaCheckCircle, FaRegCheckCircle, FaAngleDoubleRight } from 'react-icons/fa';
import { BiCircle } from 'react-icons/bi';

export default function OnboardingForm(props) {
    // ref
    const formRef = useRef(null);
    // type of fields allowed for validation
    const allowedForValidation = ['text', 'number', 'tel', 'email', 'url'];
    // redux dispatch
    const dispatch = useDispatch();
    // ->> redux slice instance  . 
    const authRedux = useSelector((state) => state.auth.value);
    const membershipRedux = useSelector((state) => state.membership.value);
    const familyMembersRedux = useSelector((state) => state.familyMembers.value);
    const { setDashboardView } = useMember();
    let me = authRedux?.me;
    const uuid = me?.user?.uuid;
    const maxFiles = 5;

    // onboarding data state persist
    let saveData = storage.getItem('_onBoardingData');

    // steps
    const [step, setStep] = React.useState(saveData?.step ?? 0);
    const [stepper, setStepper] = React.useState(saveData?.stepper ?? 0);
    const [stepHistory, setStepHistory] = React.useState(saveData?.stepHistory ?? 0);
    const steps = [
        'Welcome',
        'Profile',
        'Address',
        'Family',
        'Financial',
        'ID Card',
        'Signatures',
        'Address Sketch',
        'Complete'
    ];

    // Basic Profile   
    const [email, setEmail] = React.useState(saveData?.email ?? "");
    const [fbLink, setFbLink] = React.useState(saveData?.fbLink ?? "");
    const [lastName, setLastName] = React.useState(saveData?.lastName ?? "");
    const [firstName, setFirstName] = React.useState(saveData?.firstName ?? "");
    const [middleName, setMiddleName] = React.useState(saveData?.middleName ?? "");
    const [nameSuffix, setNameSuffix] = React.useState(saveData?.nameSuffix ?? "");
    const [gender, setGender] = React.useState(saveData?.gender ?? "");
    const [nationality, setNationality] = React.useState(saveData?.nationality ?? "");
    const [civilStatus, setCivilStatus] = React.useState(saveData?.civilStatus ?? "");
    const [contactNumber, setContactNumber] = React.useState(saveData?.contactNumber ?? "");
    const [birthdate, setBirthdate] = React.useState(Date.parse(new Date(saveData?.birthdate)) ?? "");

    // Permanent Address
    const [zipcode, setZipcode] = React.useState(saveData?.zipcode ?? "");
    const [street, setStreet] = React.useState(saveData?.street ?? "");
    const [brgy, setBrgy] = React.useState(saveData?.brgy ?? "");
    const [city, setCity] = React.useState(saveData?.city ?? "");
    const [province, setProvince] = React.useState(saveData?.province ?? "");

    // Present Address
    const [sameWithPermanent, setSameWithPermanent] = React.useState(saveData?.sameWithPermanent ?? false);
    const [presentZipcode, setPresentZipcode] = React.useState(saveData?.presentZipcode ?? "");
    const [presentStreet, setPresentStreet] = React.useState(saveData?.presentStreet ?? "");
    const [presentBrgy, setPresentBrgy] = React.useState(saveData?.presentBrgy ?? "");
    const [presentCity, setPresentCity] = React.useState(saveData?.presentCity ?? "");
    const [presentProvince, setPresentProvince] = React.useState(saveData?.presentProvince ?? "");

    // Work or Business Financial Details
    const [sourceOfFunds, setSourceOfFunds] = React.useState(saveData?.sourceOfFunds ?? "");
    const [position, setPosition] = React.useState(saveData?.position ?? "");
    const [occupation, setOccupation] = React.useState(saveData?.occupation ?? "");
    const [employmentType, setEmploymentType] = React.useState(saveData?.employmentType ?? "");
    const [employmentTypeLabel, setEmploymentTypeLabel] = React.useState(saveData?.employmentTypeLabel ?? "");
    const [businessType, setBusinessType] = React.useState(saveData?.businessType ?? "");
    const [businessTypeLabel, setBusinessTypeLabel] = React.useState(saveData?.businessTypeLabel ?? "");
    const [organizationType, setOrganizationType] = React.useState(saveData?.organizationType ?? "");
    const [organizationTypeLabel, setOrganizationTypeLabel] = React.useState(saveData?.organizationTypeLabel ?? "");
    const [lengthInService, setLengthInService] = React.useState(saveData?.lengthInService ?? "");
    const [yearOrMonth, setYearOrMonth] = React.useState(saveData?.yearOrMonth ?? "");
    const [companyName, setCompanyName] = React.useState(saveData?.companyName ?? "");
    const [companyAddress, setCompanyAddress] = React.useState(saveData?.companyAddress ?? "");
    const [salary, setSalary] = React.useState(saveData?.salary ?? "");
    const [country, setCountry] = React.useState(saveData?.country ?? "Philippines");

    // Files 
    const [avatar, setAvatar] = React.useState(saveData?.avatar ?? null);
    const [avatarPreview, setAvatarPreview] = React.useState(saveData?.avatarPreview ?? null);
    const [idPresented, setIdPresented] = React.useState(saveData?.idPresented ?? []);
    const [idPresentedPreview, setIdPresentedPreview] = React.useState(saveData?.idPresentedPreview ?? []);
    const [signatures, setSignatures] = React.useState(saveData?.signatures ?? []);
    const [signaturesPreview, setSignaturesPreview] = React.useState(saveData?.signaturesPreview ?? []);
    const [sketches, setSketches] = React.useState(saveData?.sketches ?? []);
    const [sketchesPreview, setSketchesPreview] = React.useState(saveData?.sketchesPreview ?? []);

    const validateForm = (elements) => {
        // check if there are elements 
        if (Object.keys(elements).length === 0) {
            return true;
        }

        for (var i = 0, element; (element = elements[i++]);) {
            if (element.nodeName.toLowerCase() === "input" && allowedForValidation.includes(element.type) && element.required) {
                let testValue = element.value.replace(/\s+/g, '');
                if (testValue === "" || element.value === undefined || !element.validity.valid) {
                    element.reportValidity();
                    return false;
                }
            }
        }
        return true;
    }

    const handleError = (message) => {
        toastService.modalError(message, "top-right", 0, true, false);
    }

    const handleInfo = (message) => {
        toastService.modalInfo(message, "top-right", 0, true, false);
    }

    const handleStart = () => {
        handleSteps(step + 1);
    }

    const handleSkip = () => {
        dispatch(setIsFirstLogin(1));
        props.toggle();
    }

    const handleContinueLater = async () => {
        let choice = await alertService.confirm({ title: "Continue later?", text: "The files you selected (e.g. Digital ID, Sketch, Signatures) are not saved and should be re-selected" });
        if (choice.isConfirmed) {
            saveOnboardingState();
            storage.setItem('_showOnBoard', false);
            props.toggle();
        }
    }

    const handleSteps = (value) => {
        setStep(value);
        setStepper(value);

        if (stepHistory < value) {
            setStepHistory(value);
        }

    }

    const handleStepper = (value) => {
        try {

            if (value === steps.length - 1) {
                handleInfo('Please submit the form');
                return false;
            }

            if (stepHistory < value) {
                handleInfo('Follow the steps first');
                return false;
            }

            let elements = formRef.current?.elements;
            let previousStep = stepper;
            let currentStep = value;

            if (value < previousStep) {
                handleSteps(value);
                return false;
            }

            if (previousStep !== currentStep) {
                switch (previousStep) {
                    case 0:
                        handleSteps(currentStep);
                        break;
                    case 1:
                        if (avatar === null && currentStep > stepHistory) {
                            handleError("Please set a profile picture");
                            return false;
                        }

                        if (!validateForm(elements)) {
                            handleError("Please make sure you enter inputs on required(*) fields");
                            return false;
                        }

                        handleSteps(currentStep);
                        break;
                    case 2:
                        if (!validateForm(elements)) {
                            handleError("Please make sure you enter inputs on required(*) fields");
                            return false;
                        }
                        handleSteps(currentStep);
                        break;
                    case 3:
                        if (!handleFamilyMembers()) {
                            handleError("Please add family members");
                            return false;
                        }

                        handleSteps(currentStep);
                        break;
                    case 4:

                        if (!handleSourceOfFundsCheck()) {
                            handleError("Please select a source of fund");
                            return false;
                        }

                        if (!validateForm(elements)) {
                            handleError("Please make sure you enter inputs on required(*) fields");
                            return false;
                        }
                        handleSteps(currentStep);
                        break;
                    case 5:
                        if (currentStep > previousStep && idPresented.length <= 0) {
                            handleError("Please set an ID");
                            return false;
                        }
                        handleSteps(currentStep);
                        break;
                    case 6:
                        if (currentStep > previousStep && signatures.length <= 0) {
                            handleError("Please set digital signatures");
                            return false;
                        }
                        handleSteps(currentStep);
                        break;
                    case 7:
                        if (currentStep > previousStep && sketches.length <= 0) {
                            handleError("Please set address sketch");
                            return false;
                        }
                        handleSteps(currentStep);
                        break;
                    case 8:
                        handleSteps(currentStep);
                        break;
                    default:
                        return true;
                }
            }
        } catch (error) {
            handleError(error);

        }
    }

    const handleAddress = (e) => {
        try {
            e.preventDefault();

            if (sameWithPermanent) {
                handlePresentAddress(sameWithPermanent);
            }

            if (!sameWithPermanent && (zipcode === "" || presentZipcode === "")) {
                toastService.alert("Please select a valid zipcode");
                return false;
            }

            handleSteps(step + 1);

        } catch (e) {
            toastService.alert(e.toString());
        }
    }

    const handlePresentAddress = (value) => {
        setSameWithPermanent(value);
        setPresentZipcode(zipcode);
        setPresentStreet(street);
        setPresentBrgy(brgy);
        setPresentCity(city);
        setPresentProvince(province);
    }

    const handleSourceOfFunds = (value) => {
        setSourceOfFunds(value);
    }

    const handleSourceOfFundsCheck = () => {
        if (sourceOfFunds === "") {
            return false;
        }
        return true;
    }

    const handleChangeLengthInService = (value) => {

        setYearOrMonth("");

        let serviceLength = parseInt(value);
        let label = "Month";

        if (yearOrMonth !== "") {
            if (yearOrMonth === "Year" || yearOrMonth === "Years") {
                label = "Year";
            }
        }

        // Checking if plural or not
        if (serviceLength > 1) {
            label = `${label}s`;
        }

        setLengthInService(value);
        setYearOrMonth(label);

    }

    const handleFamilyMembers = () => {
        return familyMembersRedux?.familyMembers?.length > 0 ? true : false;
    }

    const saveOnboardingState = () => {

        let old = storage.getItem('_onBoardingData');

        let data = {
            step,
            stepper,
            stepHistory,
            uuid,
            email,
            fbLink,
            lastName,
            firstName,
            middleName,
            nameSuffix,
            birthdate: format(Date.parse(new Date(birthdate)), "Y-MM-dd"),
            gender,
            nationality,
            civilStatus,
            contactNumber,
            zipcode,
            street,
            brgy,
            city,
            province,
            sameWithPermanent,
            presentZipcode,
            presentStreet,
            presentBrgy,
            presentCity,
            presentProvince,
            sourceOfFunds,
            position,
            occupation,
            employmentType,
            employmentTypeLabel,
            businessType,
            businessTypeLabel,
            organizationType,
            organizationTypeLabel,
            lengthInService,
            yearOrMonth,
            companyName,
            companyAddress,
            salary,
            country,
            avatarPreview,
            avatar,
            familyMembers: familyMembersRedux.familyMembers
        }

        let newData = { ...old, ...data };

        storage.setItem('_onBoardingData', newData);
    }

    const handleSubmit = async () => {

        if (idPresented.length <= 0) {
            handleError("Please set an ID");
            return false;
        }

        if (signatures.length <= 0) {
            handleError("Please set digital signatures");
            return false;
        }

        if (sketches.length <= 0) {
            handleError("Please set address sketch");
            return false;
        }

        if (!handleFamilyMembers()) {
            handleError("Please add family members");
            return false;
        }

        let formData = new FormData();

        let bdate = format(Date.parse(new Date(birthdate)), "Y-M-d")

        let data = {
            uuid,
            email,
            fbLink,
            lastName,
            firstName,
            middleName,
            birthdate: bdate,
            gender,
            nationality,
            civilStatus,
            contactNumber,
            zipcode,
            street,
            brgy,
            city,
            province,
            presentZipcode,
            presentStreet,
            presentBrgy,
            presentCity,
            presentProvince,
            familyMembers: JSON.stringify(familyMembersRedux.familyMembers),
            sourceOfFunds,
            employmentType,
            businessType,
            organizationType,
            occupation,
            yearOrMonth,
            lengthInService,
            companyName,
            companyAddress,
            salary,
            position,
            country,
            avatar
        }

        for (let key in data) {
            formData.append(key, data[key]);
        }

        for (let value of idPresented) {
            formData.append('idPresented[]', value);
        }

        for (let value of signatures) {
            formData.append('signatures[]', value);
        }

        for (let value of sketches) {
            formData.append('sketches[]', value);
        }

        let response = await dispatch(put({ uuid: uuid, formData })).unwrap();
        if (!response.error) {
            handleSteps(parseInt(step) + 1);
            setStepper(parseInt(step) + 2);
            dispatch(resetForm());
            dispatch(clearFamilyMembers());
            dispatch(getMe());
            storage.resetOnboard();
            setDashboardView(1);
        }

    }


    React.useEffect(() => {
        let mounted = false;
        if (!mounted) {

            if (!saveData?.email || saveData?.email === undefined) {

                let bdate = Date.parse(me?.birthdate);

                // Basic Profile  
                setEmail(me?.user.email);
                setFbLink(me?.fb_link);
                setLastName(me?.last_name);
                setFirstName(me?.first_name);
                setMiddleName(me?.middle_name ?? "");
                setNameSuffix(me?.suffix ?? "");
                setGender(me?.gender);
                setNationality(me?.nationality);
                setCivilStatus(me?.civil_status);
                setContactNumber(me?.contact_no);
                setBirthdate(bdate);

            }
        }

        return () => mounted = true;

    }, [me, saveData])

    return (
        <div style={{ minWidth: 200, minHeight: 575 }}>
            <Box sx={{ top: 30, position: 'absolute', width: '100%', p: 1, m: 0, mt: 2 }}>
                {
                    (step !== 0 && step !== 8) &&
                    <Box sx={{ width: '100%' }} display="flex" flexDirection="row" justifyContent="space-between">
                        <Box sx={{ width: '50%' }} display="flex" justifyContent="flex-start">
                            <Button
                                onClick={() => handleContinueLater()}
                                disableRipple sx={{ top: -25, left: 25, pb: 2, "&:hover": { backgroundColor: "transparent" } }}>
                                Continue later
                                <FaAngleDoubleRight style={{ marginLeft: 5 }} />
                            </Button>
                        </Box>


                    </Box>
                }
                <Stepper activeStep={stepper} alternativeLabel >
                    {steps.map((label, index) => (
                        <Step key={label} onClick={() => handleStepper(index)}>
                            <StepLabel sx={{ "&:hover": { cursor: 'pointer' } }}>{label}</StepLabel>
                        </Step>
                    ))}
                </Stepper>
            </Box>
            <Paper sx={{ p: 0, m: 0, pt: 15 }} elevation={0}>
                <Grid container spacing={2} sx={{ mt: 1 }}>
                    {step === 0 &&
                        <Grid item xs={12}>
                            <Grid container spacing={1}>
                                <Grid item xs={7}>
                                    <Box sx={{ p: 5 }}>
                                        <Box textAlign="center">
                                            <Typography variant="h4">
                                                Welcome to Bangko sa Balay Foundation Inc.
                                            </Typography>
                                            <Box sx={{ mt: 10, pl: 7, pr: 7 }} textAlign="justify">
                                                <Typography variant="h5" sx={{ fontWeight: 'none' }}>
                                                    Let us help you complete the requirements necessary for us to open your accounts.
                                                    Please don't skip any step and provide us with correct information required for us to verify your profile.
                                                </Typography>
                                            </Box>
                                            <Box sx={{ width: '100%' }} display="flex" flexDirection="column" justifyContent="center" alignItems="center">
                                                <Button
                                                    sx={{ width: 250, p: 2, mt: 5 }}
                                                    type="submit"
                                                    fullWidth
                                                    variant="contained"
                                                    color="primary"
                                                    onClick={() => handleStart()}
                                                >
                                                    Let's Start
                                                    <FaCheck style={{ marginLeft: 4 }} />
                                                </Button>
                                                <Typography sx={{ mt: 1 }}>
                                                    or
                                                </Typography>
                                                <Button
                                                    sx={{ width: 250, p: 1, mt: 1 }}
                                                    type="submit"
                                                    fullWidth
                                                    variant="text"
                                                    color="primary"
                                                    onClick={() => handleSkip()}
                                                >
                                                    Skip for now
                                                    <FaAngleDoubleRight style={{ marginLeft: 4 }} />
                                                </Button>
                                            </Box>
                                        </Box>
                                    </Box>
                                </Grid>
                                <Grid item xs={5}>
                                    <Box display="flex" flexDirection="row" justifyContent="center">
                                        <img alt="Hi" src={Hi} width={600} height={600} style={{ marginLeft: -120, marginTop: -50 }} />
                                    </Box>
                                </Grid>
                            </Grid>
                        </Grid>
                    }
                    {step === 1 &&
                        <React.Fragment>]
                            <Box sx={{ m: 5, mt: 0 }}>
                                <Grid container spacing={2}>
                                    <Grid item xs={12}>
                                        <Box display="flex" flexDirection="column">
                                            <Typography variant="h5" sx={{ ml: 2, mb: 1 }}>
                                                Profile
                                            </Typography>
                                            <Typography variant="subttitle" sx={{ ml: 2, mt: -2 }} color="#00BFA5">
                                                Please review your profile data if it is correct and edit if necessary.
                                            </Typography>
                                        </Box>
                                    </Grid>
                                    <Grid item xs={2} sx={{ pt: 2 }}>
                                        <Box sx={{ width: '100%', pt: 2 }} display="flex" flexDirection="row" alignContent="center" justifyItems="center">
                                            <ProfilePhoto avatarPreview={avatarPreview} setAvatar={setAvatar} setAvatarPreview={setAvatarPreview} loading={membershipRedux.loading} />
                                        </Box>
                                    </Grid>
                                    <Grid item xs={10}>
                                        <form ref={formRef} onSubmit={(e) => {
                                            e.preventDefault();
                                            if (avatarPreview === null || avatarPreview === "") {
                                                handleError("Please set a profile picture");

                                                return false;
                                            }

                                            handleSteps(step + 1);
                                        }} autoComplete="off">
                                            <Grid container spacing={2} sx={{ pt: 2, pb: 3 }}>
                                                <Grid item xs={12}>
                                                    <Grid container spacing={2}>
                                                        <Grid item xs={6}>
                                                            <TextField
                                                                onChange={(e) => setEmail(e.target.value)}
                                                                value={email}
                                                                variant="outlined"
                                                                size="small"
                                                                required
                                                                fullWidth
                                                                id="email"
                                                                label="Email"
                                                                name="email"
                                                                type="email"
                                                                helperText="Note: This is read-only"
                                                                inputProps={{
                                                                    readOnly: true
                                                                }}
                                                            />
                                                        </Grid>
                                                        <Grid item xs={6}>
                                                            <TextField
                                                                onChange={(e) => setFbLink(e.target.value)}
                                                                value={fbLink}
                                                                variant="outlined"
                                                                size="small"
                                                                required
                                                                fullWidth
                                                                id="fbLink"
                                                                label="FaceBook Link"
                                                                name="fbLink"
                                                                helperText="Note: Use active FB link for verification"
                                                            />
                                                        </Grid>
                                                    </Grid>
                                                    <Divider sx={{ mt: 1 }} />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <TextField
                                                        onChange={(e) => setLastName(e.target.value)}
                                                        value={lastName}
                                                        variant="outlined"
                                                        size="small"
                                                        required
                                                        fullWidth
                                                        id="lastName"
                                                        label="Last Name"
                                                        name="lastName"
                                                    />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <TextField
                                                        onChange={(e) => setFirstName(e.target.value)}
                                                        value={firstName}
                                                        variant="outlined"
                                                        size="small"
                                                        required
                                                        fullWidth
                                                        id="firstName"
                                                        label="First Name"
                                                        name="firstName"
                                                    />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <TextField
                                                        onChange={(e) => setMiddleName(e.target.value)}
                                                        value={middleName}
                                                        variant="outlined"
                                                        size="small"
                                                        fullWidth
                                                        id="middleName"
                                                        label="Middle Name"
                                                        name="middleName"
                                                    />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <TextField
                                                        onChange={(e) => setNameSuffix(e.target.value)}
                                                        value={nameSuffix}
                                                        variant="outlined"
                                                        size="small"
                                                        fullWidth
                                                        id="nameSuffix"
                                                        label="Name Suffix"
                                                        placeholder='example Jr., Sr., III'
                                                        name="nameSuffix"
                                                    />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <Gender setGender={setGender} gender={gender} />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <Nationality setNationality={setNationality} nationality={nationality} />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <CivilStatus setCivilStatus={setCivilStatus} civilStatus={civilStatus} />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <TextField
                                                        onChange={(e) => setContactNumber(e.target.value)}
                                                        value={contactNumber}
                                                        variant="outlined"
                                                        size="small"
                                                        required
                                                        fullWidth
                                                        id="contactNumber"
                                                        label="Mobile Number"
                                                        name="contactNumber"
                                                        InputProps={{
                                                            inputComponent: ContactNumber
                                                        }}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ mt: 1 }}>
                                                <Button
                                                    sx={{ width: 150, mr: 1 }}
                                                    type="button"
                                                    fullWidth
                                                    variant="contained"
                                                    color="primary"
                                                    onClick={() => handleSteps(step - 1)}
                                                >
                                                    <FaArrowLeft style={{ marginRight: 4 }} />
                                                    Back
                                                </Button>
                                                <Button
                                                    sx={{ width: 150 }}
                                                    type="submit"
                                                    fullWidth
                                                    variant="contained"
                                                    color="primary"
                                                >
                                                    Next
                                                    <FaArrowRight style={{ marginLeft: 4 }} />
                                                </Button>
                                            </Box>
                                        </form>
                                    </Grid>
                                </Grid>
                            </Box>
                        </React.Fragment>
                    }
                    {step === 2 &&
                        <Grid item xs={12}>
                            <form ref={formRef} onSubmit={(e) => handleAddress(e)} autoComplete="off">
                                <Typography variant="h6" sx={{ mb: 1, ml: 5 }}>Permanent Address</Typography>
                                <Grid container spacing={2} sx={{ p: 5, pt: 2, pb: 1 }}>
                                    <Grid item xs={4}>
                                        <TextField
                                            onChange={(e) => setStreet(e.target.value)}
                                            value={street}
                                            variant="outlined"
                                            size="small"
                                            required
                                            fullWidth
                                            id="street"
                                            label="Street/Purok/Block/Zone"
                                            name="street"
                                        />
                                    </Grid>
                                    <Grid item xs={4}>
                                        <TextField
                                            onChange={(e) => setBrgy(e.target.value)}
                                            value={brgy}
                                            variant="outlined"
                                            size="small"
                                            required
                                            fullWidth
                                            id="brgy"
                                            label="Barangay"
                                            name="brgy"
                                        />
                                    </Grid>
                                    <Grid item xs={4}>
                                        <Zipcode zipcode={zipcode} setZipcode={setZipcode} setCity={setCity} setProvince={setProvince} />
                                    </Grid>
                                    {zipcode !== "" &&
                                        <React.Fragment>
                                            <Grid item xs={4}>
                                                <TextField
                                                    onChange={(e) => setCity(e.target.value)}
                                                    value={city}
                                                    variant="outlined"
                                                    size="small"
                                                    required
                                                    fullWidth
                                                    id="city"
                                                    label="City"
                                                    name="city"
                                                    inputProps={{
                                                        readOnly: true
                                                    }}
                                                />
                                            </Grid>
                                            <Grid item xs={4}>
                                                <TextField
                                                    onChange={(e) => setProvince(e.target.value)}
                                                    value={province}
                                                    variant="outlined"
                                                    size="small"
                                                    required
                                                    fullWidth
                                                    id="province"
                                                    label="Province"
                                                    name="province"
                                                    inputProps={{
                                                        readOnly: true
                                                    }}
                                                />
                                            </Grid>
                                        </React.Fragment>
                                    }
                                </Grid>
                                <Divider sx={{ mb: 1, mt: 2, ml: 5, mr: 5 }} />
                                <Typography variant="h6" sx={{ mt: 2, mb: 1, ml: 5 }}>
                                    Present Address
                                    <FormControlLabel
                                        sx={{ ml: 1, p: 0, pr: 1, pt: 0.5 }}
                                        control={
                                            <Checkbox
                                                icon={<BiCircle size={25} />}
                                                checkedIcon={<FaCheckCircle size={25} />}
                                                checked={sameWithPermanent}
                                                onChange={() => handlePresentAddress(!sameWithPermanent)}
                                            />
                                        }
                                        label="Same with permanent"
                                    />
                                </Typography>
                                {!sameWithPermanent &&
                                    <Grid container spacing={2} sx={{ p: 5, pt: 2 }}>
                                        <Grid item xs={4}>
                                            <TextField
                                                onChange={(e) => setPresentStreet(e.target.value)}
                                                value={presentStreet}
                                                variant="outlined"
                                                size="small"
                                                required
                                                fullWidth
                                                id="street"
                                                label="Street/Purok/Block/Zone"
                                                name="street"
                                            />
                                        </Grid>
                                        <Grid item xs={4}>
                                            <TextField
                                                onChange={(e) => setPresentBrgy(e.target.value)}
                                                value={presentBrgy}
                                                variant="outlined"
                                                size="small"
                                                required
                                                fullWidth
                                                id="brgy"
                                                label="Barangay"
                                                name="brgy"
                                            />
                                        </Grid>
                                        <Grid item xs={4}>
                                            <Zipcode zipcode={presentZipcode} setZipcode={setPresentZipcode} setCity={setPresentCity} setProvince={setPresentProvince} />
                                        </Grid>
                                        {presentZipcode !== "" &&
                                            <React.Fragment>
                                                <Grid item xs={4}>
                                                    <TextField
                                                        onChange={(e) => setPresentCity(e.target.value)}
                                                        value={presentCity}
                                                        variant="outlined"
                                                        size="small"
                                                        required
                                                        fullWidth
                                                        id="city"
                                                        label="City"
                                                        name="city"
                                                        inputProps={{
                                                            readOnly: true
                                                        }}
                                                    />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <TextField
                                                        onChange={(e) => setPresentProvince(e.target.value)}
                                                        value={presentProvince}
                                                        variant="outlined"
                                                        size="small"
                                                        required
                                                        fullWidth
                                                        id="province"
                                                        label="Province"
                                                        name="province"
                                                        inputProps={{
                                                            readOnly: true
                                                        }}
                                                    />
                                                </Grid>
                                            </React.Fragment>
                                        }
                                    </Grid>
                                }

                                <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ pr: 5 }}>
                                    <Button
                                        sx={{ width: 150 }}
                                        type="button"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        onClick={() => handleSteps(step - 1)}
                                    >
                                        <FaArrowLeft style={{ marginRight: 4 }} />
                                        Back
                                    </Button>
                                    <Button
                                        sx={{ width: 150, ml: 1 }}
                                        type="submit"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                    >
                                        Next
                                        <FaArrowRight style={{ marginLeft: 4 }} />
                                    </Button>
                                </Box>
                            </form>
                        </Grid>
                    }
                    {step === 3 &&
                        <Grid item xs={12}>
                            <Grid container spacing={2} sx={{ p: 5, pt: 1, pb: 1 }}>
                                <Grid item xs={12}>
                                    <Typography variant="h6" sx={{ mb: 1, mt: 1 }}>Family Members</Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <NewFamilyMemberTable />
                                </Grid>
                            </Grid>
                            <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ pr: 5 }}>
                                <Button
                                    sx={{ width: 150 }}
                                    type="button"
                                    fullWidth
                                    variant="contained"
                                    color="primary"
                                    onClick={() => handleSteps(step - 1)}
                                >
                                    <FaArrowLeft style={{ marginRight: 4 }} />
                                    Back
                                </Button>
                                <Button
                                    sx={{ width: 150, ml: 1 }}
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    color="primary"
                                    onClick={() => {
                                        if (!handleFamilyMembers()) {
                                            handleError("Please add family members");
                                            return false;
                                        }
                                        handleSteps(step + 1);
                                    }}
                                >
                                    Next
                                    <FaArrowRight style={{ marginLeft: 4 }} />
                                </Button>
                            </Box>
                        </Grid>
                    }
                    {step === 4 &&
                        <Grid item xs={12}>
                            <form ref={formRef} onSubmit={(e) => { e.preventDefault(); handleSteps(step + 1); }} autoComplete="off">
                                <Grid container spacing={2} sx={{ p: 5, pt: 1 }}>
                                    <Grid item xs={12}>
                                        <Box display="flex" flexDirection="row" sx={{ width: 500, m: 0 }}>
                                            <Typography variant="h6" sx={{ mb: 1, mt: 1, mr: 2 }}>
                                                Source of Funds:
                                            </Typography>
                                            <Box sx={{ mt: 0.8 }}>
                                                <SourceOfFunds sourceOfFunds={sourceOfFunds.toLowerCase()} setSourceOfFunds={(value) => handleSourceOfFunds(value)} />
                                            </Box>
                                        </Box>
                                    </Grid>
                                    <Grid item xs={12}>
                                        {sourceOfFunds === "" &&
                                            <Box
                                                display="flex"
                                                flexDirection="column"
                                                justifyContent="center"
                                                alignItems="center"
                                                sx={{ backgroundColor: "#eee", minHeight: 210 }}
                                            >
                                                <Typography variant='h5'>
                                                    Please select your main source of funds
                                                </Typography>
                                            </Box>
                                        }
                                        {sourceOfFunds.toLowerCase() === "salary" &&
                                            <div>
                                                <Typography variant="h6" sx={{ mb: 1 }}>Work Details</Typography>
                                                <Grid container spacing={2}>
                                                    <Grid item xs={6}>
                                                        <EmploymentTypes setEmploymentType={setEmploymentType} employmentType={employmentType} setLabel={setEmploymentTypeLabel} />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <OrganizationTypes setOrganizationType={setOrganizationType} organizationType={organizationType} setLabel={setOrganizationTypeLabel} />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <TextField
                                                            onChange={(e) => setCompanyName(e.target.value)}
                                                            value={companyName}
                                                            variant="outlined"
                                                            size="small"
                                                            required
                                                            fullWidth
                                                            id="companyName"
                                                            label="Company"
                                                            name="companyName"
                                                        />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <TextField
                                                            onChange={(e) => setCompanyAddress(e.target.value)}
                                                            value={companyAddress}
                                                            variant="outlined"
                                                            size="small"
                                                            required
                                                            fullWidth
                                                            id="companyAddress"
                                                            label="Company Address"
                                                            name="companyAddress"
                                                        />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <TextField
                                                            onChange={(e) => setOccupation(e.target.value)}
                                                            value={occupation}
                                                            variant="outlined"
                                                            size="small"
                                                            required
                                                            fullWidth
                                                            id="occupation"
                                                            label="Occupation"
                                                            name="occupation"
                                                        />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <TextField
                                                            type="number"
                                                            onChange={(e) => setSalary(e.target.value)}
                                                            value={salary}
                                                            variant="outlined"
                                                            size="small"
                                                            required
                                                            fullWidth
                                                            id="salary"
                                                            label="Salary"
                                                            name="salary"
                                                        />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <Country country={country} setCountry={setCountry} />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <YearOrMonth
                                                            value={lengthInService}
                                                            setServiceLength={(value) => handleChangeLengthInService(value)}
                                                            yearOrMonth={yearOrMonth}
                                                            setYearOrMonth={setYearOrMonth}
                                                        />
                                                    </Grid>
                                                </Grid>
                                            </div>
                                        }
                                        {sourceOfFunds.toLowerCase() === "business" &&
                                            <div>
                                                <Typography variant="h6" sx={{ mb: 1 }}>Business Details</Typography>
                                                <Grid container spacing={2}>
                                                    <Grid item xs={6}>
                                                        <BusinessTypes setBusinessType={setBusinessType} businessType={businessType} setLabel={setBusinessTypeLabel} />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <TextField
                                                            onChange={(e) => setCompanyName(e.target.value)}
                                                            value={companyName}
                                                            variant="outlined"
                                                            size="small"
                                                            required
                                                            fullWidth
                                                            id="companyName"
                                                            label="Company"
                                                            name="companyName"
                                                        />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <TextField
                                                            onChange={(e) => setCompanyAddress(e.target.value)}
                                                            value={companyAddress}
                                                            variant="outlined"
                                                            size="small"
                                                            required
                                                            fullWidth
                                                            id="companyAddress"
                                                            label="Company Address"
                                                            name="companyAddress"
                                                        />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <TextField
                                                            onChange={(e) => setPosition(e.target.value)}
                                                            value={position}
                                                            variant="outlined"
                                                            size="small"
                                                            required
                                                            fullWidth
                                                            id="position"
                                                            label="Position"
                                                            name="position"
                                                        />
                                                    </Grid>
                                                    <Grid item xs={6}>
                                                        <YearOrMonth
                                                            value={lengthInService}
                                                            setServiceLength={(value) => handleChangeLengthInService(value)}
                                                            yearOrMonth={yearOrMonth}
                                                            setYearOrMonth={setYearOrMonth}
                                                        />
                                                    </Grid>
                                                </Grid>
                                            </div>
                                        }
                                    </Grid>
                                </Grid>
                                <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ pr: 5 }}>
                                    <Button
                                        sx={{ width: 150 }}
                                        type="button"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        onClick={() => handleSteps(step - 1)}
                                    >
                                        <FaArrowLeft style={{ marginRight: 4 }} />
                                        Back
                                    </Button>
                                    <Button
                                        sx={{ width: 150, ml: 1 }}
                                        type="submit"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                    >
                                        Next
                                        <FaArrowRight style={{ marginLeft: 4 }} />
                                    </Button>
                                </Box>
                            </form>
                        </Grid>
                    }
                    {step === 5 &&
                        <Grid item xs={12}>
                            <form onSubmit={(e) => {
                                e.preventDefault();
                                if (idPresented.length === 0) {
                                    handleError("Please include a digital copy of your valid ID");

                                    return false;
                                }

                                handleSteps(step + 1);
                            }} autoComplete="off">
                                <Typography variant="h6" sx={{ mb: 1, pl: 5 }}>ID card</Typography>
                                <Grid container spacing={2} sx={{ p: 5, pt: 1 }}>
                                    <Grid item xs={12} alignItems="center" justifyContent="center">
                                        <FileDropzone
                                            drop={(files) => setIdPresented(files)}
                                            files={idPresented}
                                            preview={(filesPreview) => setIdPresentedPreview(filesPreview)}
                                            filesPreview={idPresentedPreview}
                                            maxFiles={maxFiles}
                                            title="Drop ID here or click and select"
                                            height={250}
                                        />
                                    </Grid>
                                </Grid>
                                <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ pr: 5 }}>
                                    <Button
                                        sx={{ width: 150 }}
                                        type="button"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        onClick={() => handleSteps(step - 1)}
                                    >
                                        <FaArrowLeft style={{ marginRight: 4 }} />
                                        Back
                                    </Button>
                                    <Button
                                        sx={{ width: 150, ml: 1 }}
                                        type="submit"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                    >
                                        Next
                                        <FaArrowRight style={{ marginLeft: 4 }} />
                                    </Button>
                                </Box>
                            </form>
                        </Grid>
                    }
                    {step === 6 &&
                        <Grid item xs={12}>
                            <form onSubmit={(e) => {
                                e.preventDefault();
                                if (signatures.length === 0) {
                                    handleError("Please include a digital copy of your signatures");

                                    return false;
                                }

                                handleSteps(step + 1);
                            }} autoComplete="off">
                                <Typography variant="h6" sx={{ mb: 1, mt: 1, ml: 5 }}>Digital Signatures</Typography>
                                <Grid container spacing={2} sx={{ p: 5, pt: 1 }}>
                                    <Grid item xs={12} alignItems="center" justifyContent="center">
                                        <FileDropzone
                                            drop={(files) => setSignatures(files)}
                                            files={signatures}
                                            preview={(filesPreview) => setSignaturesPreview(filesPreview)}
                                            filesPreview={signaturesPreview}
                                            maxFiles={maxFiles}
                                            title="Drop specimen signatures here or click and select"
                                            height={250}
                                        />
                                    </Grid>
                                </Grid>
                                <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ pr: 5 }}>
                                    <Button
                                        sx={{ width: 150 }}
                                        type="button"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        onClick={() => handleSteps(step - 1)}
                                    >
                                        <FaArrowLeft style={{ marginRight: 4 }} />
                                        Back
                                    </Button>
                                    <Button
                                        sx={{ width: 150, ml: 1 }}
                                        type="submit"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                    >
                                        Next
                                        <FaArrowRight style={{ marginLeft: 4 }} />
                                    </Button>
                                </Box>
                            </form>
                        </Grid>
                    }
                    {step === 7 &&
                        <Grid item xs={12}>
                            <form onSubmit={(e) => {
                                e.preventDefault();
                                if (sketches.length === 0) {
                                    handleError("Please include a digital copy of your address sketch");

                                    return false;
                                }

                                handleSubmit();
                            }} autoComplete="off">
                                <Typography variant="h6" sx={{ mb: 1, mt: 1, ml: 5 }}>Sketches</Typography>
                                <Grid container spacing={2} sx={{ p: 5, pt: 1 }}>
                                    <Grid item xs={12} alignItems="center" justifyContent="center">
                                        <FileDropzone
                                            drop={(files) => setSketches(files)}
                                            files={sketches}
                                            preview={(filesPreview) => setSketchesPreview(filesPreview)}
                                            filesPreview={sketchesPreview}
                                            maxFiles={maxFiles}
                                            title="Drop your address sketches here or click and select"
                                            height={250}
                                        />
                                    </Grid>
                                </Grid>
                                <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ pr: 5 }}>
                                    <LoadingButton
                                        sx={{ width: 150 }}
                                        type="button"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        onClick={() => handleSteps(step - 1)}
                                        loading={membershipRedux.loading}
                                    >
                                        <FaArrowLeft style={{ marginRight: 4 }} />
                                        Back
                                    </LoadingButton>
                                    <LoadingButton
                                        sx={{ width: 150, ml: 1 }}
                                        type="submit"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        loading={membershipRedux.loading}
                                    >
                                        Submit
                                        <FaCheck style={{ marginLeft: 4 }} />
                                    </LoadingButton>
                                </Box>
                            </form>
                        </Grid>
                    }
                    {step === 8 &&
                        <Grid item xs={12}>
                            <Box display="flex" flexDirection="column" justifyContent="center" alignItems="center">
                                <FaRegCheckCircle color="#00BFA5" size={100} />
                                <Typography variant="h4" sx={{ mt: 2 }}>You have completed your profile</Typography>
                                <Typography variant="span" sx={{ mt: 1 }}>
                                    Bangko sa Balay Foundation Inc. is now notified of your application. You may check your application status on the dashboard.
                                </Typography>
                                <Button
                                    sx={{ width: 250, ml: 1, mt: 5, p: 1 }}
                                    type="button"
                                    fullWidth
                                    variant="contained"
                                    color="primary"
                                    onClick={() => props.toggle()}
                                >
                                    Go to Dashboard
                                    <FaArrowRight style={{ marginLeft: 5 }} />
                                </Button>
                            </Box>
                        </Grid>
                    }
                </Grid>
            </Paper >
        </div >
    )
}
